html {
  font-size: 62.5%; }

body {
  background: #FFF;
  font: normal 16px/1.4 Arial;
  font-size: 1.6rem;
  @include smooth-font;
  min-width: $min-width;
  color: black; }

h1:not([class]), .h1 {
  @include f(36,42,100); }

h2:not([class]), .h2 {
  @include f(48); }

h3:not([class]), .h3 {
  @include f(24,24,100); }

h4:not([class]), .h4 {
  @include f(24,36); }

h5:not([class]), .h5 {
  @include f(16,24,100); }

a:not([class]) {
  color: inherit;
  @include trans(color);
  text-decoration: none;
  &:hover {
    color: red; } }

.l {
  max-width: 1040px;
  padding: 0 20px;
  margin: auto;
  box-sizing: border-box; }

.align-center {
  text-align: center; }

.align-right {
  text-align: right; }

.uppercase {
  text-transform: uppercase; }

.btn {
  display: inline-block;
  box-sizing: border-box;
  position: relative;
  z-index: auto;
  background: none;
  border: 1px solid;
  color: inherit;
  height: 48px;
  text-align: center;
  @include f(18, false, 20);
  line-height: 46px;
  font-weight: 700;
  text-decoration: none;
  cursor: pointer;
  @include trans;
  > span {
    position: relative;
    z-index: 1; }
  &:before {
    content: "";
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: none;
    transform: scale(0.5);
    opacity: 0;
    visibility: hidden;
    @include trans; }

  &:hover,
  &:focus {
    border-color: #ff0000;
    color: white;
    &:before {
      opacity: 1;
      visibility: visible;
      background: #ff0000;
      transform: scale(1); } }

  &:active {
    border-color: #f10000;
    color: white;
    &:before {
      background: #f10000; } }

  &.is-active {
    border-color: black;
    color: white;
    &:before {
      background: black;
      opacity: 1;
      visibility: visible;
      transform: scale(1); } }

  &[disabled],
  &.is-disabled {
    background: none;
    color: black;
    border-color: black;
    opacity: 0.2;
    cursor: not-allowed;
    &:before {
      display: none; } }

  &_hero {
    min-width: 303px; } }
