.modal-video {
  position: fixed;
  z-index: 9;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: black;
  color: white; }

.modal-video__content {
  height: 100%;
  min-width: $min-width; }

.modal-video__container {
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: img-url('video-placeholder.jpg') no-repeat center;
  background-size: cover; }

.video-section {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(black, 0.7);
  @include vertical; }

.video-section__center {
  display: inline-block;
  vertical-align: middle;
  @include f(16); }

.controll-btn {
  color: white;
  background: none;
  line-height: 1;
  @include trans;
  .icon {
    vertical-align: top;
    fill: currentColor; }
  &:hover {
    color: $color-red; } }

.controll-btn-close {
  position: absolute;
  top: 30px;
  right: 30px;
  @include f(30); }

.controll-btn-volume {
  position: absolute;
  bottom: 20px;
  right: 26px;
  @include f(24);
  padding-right: 11px;
  &:before,
  &:after {
    content: "";
    position: absolute;
    top: 50%;
    width: 2px;
    height: 8px;
    background: currentColor;
    margin-top: -4px;
    transition: all 0.2s ease;
    transition-property: transform, opacity, visibility; }
  &:before {
    right: 5px; }
  &:after {
    right: 0; }

  &.level-0 {
    &:before {
      transform: translateX(4px) rotate(45deg) scaleY(1.7); }
    &:after {
      transform: translateX(-1px) rotate(-45deg) scaleY(1.7); } }

  &.level-1 {
    &:after {
      opacity: 0;
      visibility: hidden; } }

  &.level-2 {
    &:after {
      transform: scaleY(1.75); } } }


.step-0 {
  .icon {
    fill: white; }
  .icon-r-dot-o {
    @include f(210); }
  .icon-mouse {
    margin-top: 52px;
    @include f(52); }
  p {
    @include f(18);
    font-weight: 700;
    margin: 1em 0; } }

.step-0-logo {
  display: inline-block;
  position: relative;
  margin-bottom: 122px;
  position: relative;
  .h3 {
    text-align: left;
    position: absolute;
    left: 100%;
    bottom: 7px;
    margin-left: 22px; } }
